import React from 'react'
import Navbar from './Navbar';
import About from './About'
import Myhobbies from './myhobbies';
import ContactMe from './ContactMe'
import { FaLongArrowAltRight } from 'react-icons/fa';
import TypeWrting from './TypeWrting';
import Achievements from './Achievements'
import Development from './Development';


const Landinghomepage = () => {
  return (
    <div className='main-section'>
      <Navbar />
      <div className='main-content'>
        <div className='heading-div'>
          <h1>I'm<span className='white'><TypeWrting /></span></h1>
          <p>IT student</p>
          <div>
            <a href='#My-CV' className='btn'>Download my CV <FaLongArrowAltRight className='arrow' /></a>
            <div style={{ width: '50rem', margin: 'auto' }} className="lottie-anime"><Development /></div>
          </div>

        </div>
        <div id="about" className='about-section'>
          <About />
        </div>
        <div id="credentials">
          <Achievements />
        </div>
        <div id="hobbies" className='myhobbies-section'>
          <Myhobbies />
        </div>
        <div id='workwithme'>
          <ContactMe />
        </div>
      </div>
    </div>
  )
}

export default Landinghomepage
