import React from 'react'
import Music from './Music'
import Ball from './Balls'
import Play from './Playing'
import Card from 'react-bootstrap/Card'

const myhobbies = () => {
    return (
        <div>
            <h2><span className='style-color'>My</span> hobbies</h2>
            <div className='grid-col'>
                <div>
                    <p>Listen Music</p>
                    <Card style={{ width: '20rem' }}>
                    <div><Music/></div>
                    </Card> 
                </div>
                <div>
                    <p>Ball Sports</p>
                    <Card style={{ width: '20rem' }}>
                    <Ball/>
                    </Card>
                </div>
                <div>
                    <p>Online Games</p>
                    <Card style={{ width: '25.5rem' }} className="online-games">
                    <Play/>
                    </Card>
                </div>
            </div>
        </div>
    )
}

export default myhobbies
