import React from 'react'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Tabs from 'react-bootstrap/Tabs'
import Tab from 'react-bootstrap/Tab'
import CSU from "../CSU.png"
import Tali from "../Taligaman.jpg"
import Elem from "../elementary.jpg"
import Myskills from './Myskills'

const Credentials = () => {
  return (
    <div>
      <h2>Achive<span className='style-color'>ments</span></h2>
      <Container>
        <Tabs defaultActiveKey="Educational Attainment" id="uncontrolled-tab-example" className="mb-3">
          <Tab eventKey="Educational Attainment" title="Educational Attainment">
            <div className='Education'>
              <Row>
                <h3>Educational Attainment</h3>
                <Col sm={4}><p>Tertiary Educational</p></Col>
                <Col sm={8}><p>Caraga State University <img src={CSU} width={50}/></p><br /><p>(2018)</p></Col>
              </Row>
              <Row>
                <Col sm={4}><p>Senior High Education</p></Col>
                <Col sm={8}><p>Taligaman National High School <img src={Tali} width={50}/></p><br /><p>(2016)</p></Col>
              </Row>
              <Row>
                <Col sm={4}><p>Secondary Education</p></Col>
                <Col sm={8}><p>Taligaman National High School <img src={Tali} width={50}/></p><br /><p>(2012)</p></Col>
              </Row>
              <Row>
                <Col sm={4}><p>Elementary School</p></Col>
                <Col sm={8}><p>Taligaman National High School <img src={Elem} width={50}/></p><br /><p>(2006)</p></Col>
              </Row>
            </div>
          </Tab>
          <Tab eventKey="Skills" title="Skills">
            <div className='Skills'>
              <Row>
                <h3>Skills</h3>
                <Myskills/>
              </Row>
            </div>
          </Tab>
        </Tabs>
      </Container>
    </div>
  )
}

export default Credentials
