import React, { useEffect } from 'react';
import ReactDOM from 'react-dom';
import Landinghomepage from './Landinghomepage'

const Home = () => {
    return (
        <>  
            <Landinghomepage/>
        </>
    )
}

const HomeTitle = () => {
    useEffect(() => {
        document.title = "Rorey MP"
    }, [])
};

ReactDOM.render(
    <HomeTitle />,
    document.getElementById('root')
);


export default Home
