import React from 'react'
import Card from 'react-bootstrap/Card';
import webdev from "../webdev.jpg"
import seo from "../seo.png"
import security from "../security.jpg"


const Myskills = () => {
    return (
        <Card style={{ width: '100%', margin:"auto;"}} className="div-skills">           
            <div className='cont-card-myskills'>               
                <div className='hover-effect'>
                <Card.Img variant="top" src={webdev} width={300}/>
                </div>
                <Card.Body className='myskill-body'>
                    <Card.Title className='color-white'>Web Development</Card.Title>
                </Card.Body>
            </div>
            <div className='cont-card-myskills' >
                <div className='hover-effect'>
                <Card.Img variant="top" src={seo} width={300}/>
                </div>
                <Card.Body className='myskill-body'> 
                    <Card.Title className='color-white'>Search Engine Optimazation</Card.Title>
                </Card.Body>
            </div>
            <div className='cont-card-myskills'>
                <div className='hover-effect'>
                <Card.Img variant="top" src={security} width={100} />
                </div>
                <Card.Body className='myskill-body'>
                    <Card.Title className='color-white' >Web Security</Card.Title>
                </Card.Body>
            </div>
        </Card>

    )
}

export default Myskills
