import React from 'react'
import Typed from "react-typed"

const TypeWrting = () => {
  return (
    <div>
      <Typed
      className='type-txt'
      strings={["Wordpress Developer", "Kajabi Developer", "Shopify Developer", "Web Designer"]}
      typeSpeed={40}
      backSpeed={60}
      loop
      />
    </div>
    
  )
}

export default TypeWrting
